import React from 'react';
import Carousel from 'react-slick';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Link } from 'gatsby'
import classnames from 'classnames';
let ArrowButton = ({ arrow, className, ...props }) => (
  <button
    {...props}
    className={`${className} btn-arrow ${arrow}`}
  />
);

let getBackgroundStyle = (img) => {
  return {
    width: '100vw',
    height: '90vh'
  }
}

export default function CoverHero({
  data = [],
  render = () => null
}) {
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: true,
    nextArrow: (<ArrowButton arrow={'right'}>
      <FontAwesomeIcon icon={["fas", "arrow-right"]} style={{color:"#000000"}} />
    </ArrowButton>),
    prevArrow: (<ArrowButton arrow={'left'}>
      <FontAwesomeIcon icon={["fas", "arrow-left"]} style={{color:"#000000"}} />
    </ArrowButton>),
    appendDots: (dots) => <div className="cover-hero__dots"><ul>{dots}</ul></div>,
    customPaging:() => <button className="cover-hero__dot" />
  };
  return (
    <section className={classnames("cover-hero", {mobile: matchMobile})}>
      <Carousel {...settings}>
        {
          data.map((item) => (
            <div className="cover-hero__item" >
              <img src={item.image} style={getBackgroundStyle()} />
              <div className="cover-hero__content">
              <h1>{item.title}</h1>
              { item.secondaryText ? (<p>{item.secondaryText}</p>) : null }
              { item.buttonText && item.buttonText !== " " ? (<Link to="/nosotros" className="btn btn-secondary">{item.buttonText}</Link>) : null }
              </div>
            </div>
          ))
        }
      </Carousel>
    </section>
  )
};
