import React from 'react';
// import PropTypes from 'prop-types';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

const Video = ({ videoSrcURL, videoTitle, ...props }) => (
  <div className="video">
    <iframe
      src={videoSrcURL}
      title={videoTitle}
      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
      frameBorder="0"
      webkitallowfullscreen="true"
      mozallowfullscreen="true"
      allowFullScreen
    />
  </div>
);

function VideoGallery({ gallery }) {
  let [showVideo, setShowVideo] = React.useState(false);
  let toggleVideo = () => setShowVideo(!showVideo);
  let styles = {
    backgroundImage: `url(${gallery.thumb})`,
    backgroundPosition: '50% 50%',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    padding: 0
  };

  return (
    <section className="video-gallery" style={styles} >
      {
        showVideo ? <Video videoSrcURL={gallery.video} videoTitle={gallery.header} /> : (
          <button className="btn-ghost" onClick={toggleVideo}>
            <div className="video-gallery__cover">
              <h1>{gallery.header}</h1>
              <p>{gallery.description}</p>
              <FontAwesomeIcon icon={["fas", "play-circle"]} style={{color:"#ffffff"}} />
            </div>
          </button>
        )
      }
    </section>
  )
}

VideoGallery.propTypes = {

}

export default VideoGallery

