import React from 'react';
import PropTypes from 'prop-types';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab, faYoutube } from '@fortawesome/free-brands-svg-icons';
import { faBars, faArrowRight, faArrowLeft, faPlayCircle, faChevronUp, faChevronDown, faTimesCircle, faTimes, faFileDownload, faTh, faBorderAll, faSearch, faFilePdf } from '@fortawesome/free-solid-svg-icons'
import Helmet from 'react-helmet';
import get from 'lodash/get';

import Header from '../header';
import Footer from '../footer';

library.add(fab, faBars, faArrowRight, faArrowLeft, faPlayCircle, faChevronUp, faChevronDown, faTimesCircle, faTimes, faFileDownload, faTh, faBorderAll, faSearch, faYoutube, faFilePdf);

let MetaContext = React.createContext({});

const MetaProvider = (props) => {
  let [metadata, setMetaData] = React.useState({});

  return <MetaContext.Provider {...props} value={{metadata, setMetaData}} />
}

export const useMeta = () => {
  let context = React.useContext(MetaContext);
  return context;
}

const Head = (props) => {
  let { pageContext } = props;

  let { metadata } = useMeta();
  return (
    <Helmet>
      <title>Three Bond MX</title>
      <meta name="robots" content="INDEX,FOLLOW" />
      <meta name="description" content={getDescription(pageContext, metadata)} />
      <meta property="og:title" content="Three Bond MX" />
      <meta property="og:description" content={getDescription(pageContext, metadata)} />
      <meta property="og:image" content={getImage(pageContext, metadata)} />
      <meta property="og:url" content={getCanonical(props)} />
      <meta property="og:type" content="website" />
      <meta property="og:locale" content="es_MX" />
      <meta property="og:site_name" content="Three Bond MX" />
    </Helmet> 
  )
}

const Layout = ({ children, is404, ...props }) => {
  return (
    <MetaProvider>
      {!is404 ? <Header {...props} /> : null}
      <div>
        <main>{children}</main>
        {!is404 ? <Footer {...props} /> : null}
      </div>
      <Head {...props} />
    </MetaProvider>
  )
}

function getDescription(page, metadata) {
  let name = get(page, 'name', 'default');
  switch (name) {
    case 'productsView':
      return metadata.description;
    case 'cotiza':
      return `Three Bond comprometido con ofrecerte el mejor servicio pone a tu disposicin esta seccin para que obtengas la informacin que requieres y las soluciones correctas a tus dudas respecto a los productos y aplicaciones.
      Por favor detalla tus dudas y te contactaremos de inmediato.
      Somos la mejor opcin, acrcate a nosotros.`
    case 'biblioteca':
      return `Three Bond deja a tu alcance los documentos tcnicos, de seguridad y algunos videos que sean de ayuda y te apoyen en la bsqueda de lo que necesitas.`
    default: 
    return `Los productos de Three Bond ahora se utilizan en una amplia variedad de aplicaciones. Se utilizan en automviles y otros vehculos, equipos de transporte, materiales de construccin pblicos, materiales de construccin, equipos elctricos y electrnicos, y altas tecnologas. Los productos ThreeBond se han convertido en una necesidad en los procesos de produccin de varios productos en muchas reas.`;
  }
}

function getImage(pageContext, metadata) {
  let name = get(pageContext, 'name', 'default');
  switch(name) {
    default: return `https://threebond.com/wp-content/themes/threebond/img/header-logo.png`;
  }
}

function getCanonical(context) {
  return `https://threebond.com.mx${context.path}`;
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
